<template>
    <main class="app-main">
        <section class="top">
            <p class="title">
                <md-icon class="go-back" name="arrow-left" size="lg" @click="goBack"></md-icon>
                视频中心
                <img class="search" src="../../assets/technology/icon_search@2x.png" alt="" @click="goSearch">
            </p>
        </section>
        <section class="bottom">
            <div class="md-example-child md-example-child-scroll-view md-example-child-scroll-view-0" :style="{height: Height}">
                <md-scroll-view @end-reached="$_onEndReached" :auto-reflow="true" ref="scrollView" :scrolling-x="false" @scroll="$_onScroll">
                    <div
                            v-for="i in list"
                            class="scroll-view-item"
                            :key="i.id"
                            @click="$_onItemClick(i)">
                        <img class="left-img" :src="i.coverPicture" alt="">
                        <div class="article">
                            <p v-text="i.title"></p>
                            <span><img src="@/assets/home/icon_time@2x.png" alt="">{{i.createTime}}</span>
                        </div>
                    </div>
                    <md-scroll-view-more slot="more" :is-finished="isFinished">
                    </md-scroll-view-more>
                </md-scroll-view>
            </div>
        </section>
    </main>
</template>

<script>
import { getList } from '@/apiUrl/information'
import { TabBar, Swiper, SwiperItem, ScrollView, ScrollViewMore, DropMenu, Icon, Toast } from 'mand-mobile'
export default {
  name: 'Devices',
  components: {
    [TabBar.name]: TabBar,
    [Swiper.name]: Swiper,
    [SwiperItem.name]: SwiperItem,
    [ScrollView.name]: ScrollView,
    [ScrollViewMore.name]: ScrollViewMore,
    [DropMenu.name]: DropMenu,
    [Icon.name]: Icon,
    [Toast.name]: Toast
  },
  data () {
    return {
      searchParams: {
        pageSize: 10,
        pageNumber: 1,
        typeId: 5
      },
      // tab项
      list: [],
      show: false,
      isFinished: false,
      Height: '900px'
    }
  },
  mounted () {
    this.getList()
    this.Height = window.innerHeight - 44 + 'px'
  },
  methods: {
    getList () {
      getList(this.searchParams).then(res => {
        this.list = res.data
      }).catch(err => {
        Toast.failed(err)
      })
    },
    // 跳转新闻详情页
    $_onItemClick (i) {
      this.$router.push({
        path: '/video/detail',
        query: { id: i.id }
      })
    },
    $_onEndReached () {
      if (this.isFinished) {
        return
      }
      this.searchParams.pageNumber++
      getList(this.searchParams).then(res => {
        this.list = this.list.concat(res.data)
        if (res.data.length === 0) {
          this.isFinished = true
        }
        this.$refs.scrollView.finishLoadMore()
      }).catch(err => {
        Toast.failed(err)
      })
    },
    $_onScroll ({ scrollLeft, scrollTop }) {
      // console.log(`[Mand Mobile ScrollView - demo0 - onScroll] scrollLeft: ${scrollLeft}, scrollTop: ${scrollTop}`)
    },
    // 回退
    goBack () {
      this.$router.push('/home')
    },
    // 跳转关键字检索页
    goSearch () {
      this.$router.push('/search/index')
    }
  }
}
</script>

<style lang="stylus" scoped>
    .app-main
        padding-top 88px
        height 100%
        margin-bottom 0
        .middle
            border-bottom 1px solid rgba(153, 153, 153, 0.6)
            .md-drop-menu
                height 80px
                border-bottom 1px solid #eeeeee
                .md-drop-menu-bar
                .bar-item
                    border-right 1px solid #eeeeee
        .bottom
            width 690px
            margin 0 auto
    .md-example-child-scroll-view-0
        height 100%
        background #FFF
        .scroll-view-item
            padding  30px 0 30px 0
            text-align center
            font-size 28px
            font-family DINAlternate-Bold
            height 240px
            border-bottom 2px solid rgba(153, 153, 153, 0.4)
            label
                position absolute
                color #ffffff
                background:linear-gradient(0deg,rgba(202,2,57,1),rgba(255,97,53,1))
                width 100px
                height 32px
                line-height 32px
                left 8px
                font-size 20px
                font-family PingFang-SC-Regular
                border-radius 0 0 4px 4px
            .left-img
                background url("../../assets/video/video_play_icon@2x.png") no-repeat top center
    .scroll-view-container
        div
            display flex
            justify-content space-between
            img
                width 260px
            .article
                width 410px
                text-align left
                margin 10px 0
                display flex
                flex-direction column
                justify-content space-between
                p
                    color #333333
                    font-size 28px
                    font-weight Medium
                span
                    display flex
                    align-items center
                    line-height 24px
                    font-size 24px
                    img
                        width 24px
                        margin-right 10px
        .md-scroll-view-more
            justify-content center
    .md-drop-menu
        font-size 28px
        top 88px
        z-index 8
    /deep/ .md-drop-menu-list
        padding-top 170px!important
    /deep/.scroll-view-container
        margin-top 28px
</style>
